.contacts {
  padding: 60px 120px 62px;
  color: #102654;
  position: relative;

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background: rgba(28,51,116,.2);
    position: absolute;
    left: 0;
    top: 1px;

    @include vp-1365 {
      display: none;
    }
  }

  @include vp-1365 {
    padding: 103px 133px 99px;
  }
}

.contacts__title {
  font-family: $ff-base;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin: 0;
  margin-bottom: 40px;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.75px;
    margin-bottom: 41px;
  }
}

.contacts__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 120px;
  row-gap: 15px;
  position: relative;

  @include vp-1365 {
    row-gap: 27px;
  }

  &::before {
    content: "";
    display: block;
    width: 1px;
    max-height: 117px;
    height: 100%;
    background: #74819c;
    position: absolute;
    left: 48%;
    top: 2px;

    @include vp-1365 {
      max-height: 137px;
      left: 50%;
      top: 0;
    }
  }
}

.contacts__subtitle {
  margin: 0;
  font-size: 24px;
  font-weight: 400;
  margin-bottom: 6px;

  @include vp-1365 {
    font-size: 22px;
    line-height: 22px;
    margin-bottom: 11px;
  }
}

.contacts__description,
.contacts__link {
  font-size: 14px;
  line-height: 22px;
  margin: 0;
  display: block;
  min-width: 170px;
}
